.services{
    padding: 0 3rem 0 3rem;
    display:flex;
    height: 90vh;
    margin-bottom: 8rem;
    margin-top: 10rem;
    margin-bottom: 13rem;
}

.s-left{
    display: flex;
    flex-direction: column;
    position: relative;
    max-width: 800px;
}

.s-left>:nth-child(1){
    color: var(--purple);
    font-size: 2.5rem;
    font-weight: bold;
}

.s-left>:nth-child(2){
    color: var(--gray);
    font-size: 14px;
    margin-top: 1rem;
}

.s-button{
    width: 8rem;
    height: 2rem;
    margin-top: 1rem;
}

/* blur */
.s-blur2 {
    left: 14rem;
    top: 8rem;
    opacity: .35;
  }
  
  .s-blur1 {
    top: 13rem;
    left: -18rem;
  }

  @media screen and (max-width: 480px) {
    .services {
      margin-top: 0;
      flex-direction: column;
      gap: 5rem;
      height: 66rem;
      padding: 0;
    }
  
    .cards {
      display: flex;
      flex-direction: column;
      gap: 17rem;
    }
    .cards > * {
      position: static;
    }
  }



